.header {
    padding: 7px 0;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: var(--color-brand-dark);

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem 0;
    }
}
